import React,{useContext,useState,useEffect} from 'react';
import { MyContext } from '../../contexts/MyContext';
import { WEB_URL_UPLOAD } from '../../contexts/Apilinks';
import {Link } from "react-router-dom"; 
import Navbarmenu from "../menu/Navbarmenu";
import Footer from "../Footer";
import SearchHome from "../search/SearchHome";

let BaseURLuploads = WEB_URL_UPLOAD;

const SearchPage = (props) => {
  
  useEffect(() => {
     window.scrollTo(0, 0);
   }, []);
  //console.log(props.location.search);

  let paramsString = props.location.search;
  let searchParams = new URLSearchParams(paramsString);
  let type = searchParams.get('type'); 
  let cityName = searchParams.get('cityName'); 
  let property = searchParams.get('property'); 
  let budgetMin = searchParams.get('budgetMin'); 
  let budgetMax = searchParams.get('budgetMax'); 
  let occupancy = searchParams.get('occupancy'); 
  let pgboys = searchParams.get('pgboys'); 
  let pggirls = searchParams.get('pggirls'); 

  // for (let p of searchParams) {
  //   console.log(p);
  // }
  

  return (
    <div className="search__pages">
     <Navbarmenu />
     {occupancy === 'Occupancy Type'? <>   
        {type !== undefined && cityName !== undefined  && pgboys !== undefined ? <>  
          <SearchHome pages='pg' typesss={type} cityNamesss={cityName} pgboysss={pgboys} pggirlsss={pggirls} budgetMinsss={budgetMin} budgetMaxsss={budgetMax}/>
        </> : null}
     </> : <>
        {type !== undefined && cityName !== undefined && property !== undefined ? <>  
          <SearchHome pages='sale' typesss={type} cityNamesss={cityName} propertysss={property} budgetMinsss={budgetMin} budgetMaxsss={budgetMax}/>
        </> : null}

     </> }

      
     <Footer />      
    </div>
  )
}

export default SearchPage
